import React from 'react';

const style = {
    display: 'flex',
    flexDirection: 'column',
    flexWrap: 'wrap',
    justifyContent: 'space-between',
    alignItems: 'center',
    width: '100vw',
}

const Info = ({ name, price }) => {
    return(
       <div style={style}>
            <h2>{name}</h2>
            <h3>R{price}</h3>
        </div> 

    )
}

export default Info;