import React from 'react';
import './Slider.css'
import 'tachyons';

const Slide = ({ source, name, price, id }) => {
        
        return (
        <div className = "container">
                <div id="slide" className="tc" >
                        <img src={source}  alt=""/>
                </div> 
  
        </div>

        );
}

export default Slide;